<template>
  <NavBar>
    <template v-slot:default>å¾ä¹¦åå</template>
  </NavBar>

  <TabControl
    v-show="isTabFixed"
    @tabClick="tabClick"
    :titles="['çé', 'æ°ä¹¦', 'ç²¾é']"
    style="margin-top:45px"
  ></TabControl>

  <div class="wrapper">
    <div class="content">
      <div ref="banref">
        <HomeSwiper :banners="banners"></HomeSwiper>
        <RecommendView :recommends="recommends"></RecommendView>
      </div>
      <TabControl @tabClick="tabClick" :titles="['çé', 'æ°ä¹¦', 'ç²¾é']"></TabControl>
      <GoodList :goods="showGoods"></GoodList>
    </div>
  </div>
  <BackTop @bTop="bTop" v-show="isShowBackTop"></BackTop>
</template>

<script>
import HomeSwiper from './ChildComps/HomeSwiper.vue'
import NavBar from 'components/common/navbar/NavBar'
import RecommendView from './ChildComps/RecommendView'
import TabControl from 'components/content/tabControl/TabControl'
import GoodList from 'components/content/goods/GoodList'
import BackTop from 'components/common/backtop/BackTop'
import { getHomeAllData, getHomeGoods } from 'network/home'
import { computed, nextTick, onMounted, reactive, ref, watchEffect } from 'vue'
import BScroll from 'better-scroll'

export default {
  name: 'Home',
  components: {
    NavBar,
    RecommendView,
    TabControl,
    GoodList,
    BackTop,
    HomeSwiper
  },
  setup() {
    let isTabFixed = ref(false)
    let isShowBackTop = ref(false)
    let banref = ref(null)
    const recommends = ref([])

    // åååè¡¨æ°æ®æ¨¡å
    const goods = reactive({
      sales: { page: 1, list: [] },
      new: { page: 1, list: [] },
      recommend: { page: 1, list: [] }
    })
    let bscroll = reactive({})
    let banners = ref([])
    onMounted(() => {
      getHomeAllData().then((res) => {
        recommends.value = res.goods.data
        banners.value = res.slides
      })
      getHomeGoods('sales').then((res) => {
        goods.sales.list = res.goods.data
      })
      getHomeGoods('new').then((res) => {
        goods.new.list = res.goods.data
      })
      getHomeGoods('recommend').then((res) => {
        goods.recommend.list = res.goods.data
      })

      // åå»ºBetterScrollå¯¹è±¡
      bscroll = new BScroll(document.querySelector('.wrapper'), {
        probeType: 3,
        click: true,
        pullUpLoad: true
      })

      // è§¦åæ»å¨äºä»¶
      bscroll.on('scroll', (position) => {
        isShowBackTop.value = isTabFixed.value = -position.y > banref.value.offsetHeight
      })

      // ä¸æå è½½æ°æ®ï¼è§¦åpullingUp
      bscroll.on('pullingUp', () => {
        console.log('ä¸æå è½½æ´å¤')

        const page = goods[currentType.value].page + 1
        getHomeGoods(currentType.value, page).then((res) => {
          goods[currentType.value].list.push(...res.goods.data)
          goods[currentType.value].page += 1
        })

        // å®æä¸æï¼ç­æ°æ®è¯·æ±å®æï¼è¦å°æ°æ°æ®å±ç¤ºåºæ¥
        bscroll.finishPullUp()

        // console.log('å½åç±»å:' + currentType.value + ',å½åé¡µï¼' + page)

        // éæ°è®¡ç®é«åº¦
        bscroll.refresh()
        // console.log(
        //   'contentTheHeightï¼' + document.querySelector('.content').clientHeight
        // )
      })
    })
    const tabClick = (index) => {
      let types = ['sales', 'new', 'recommend']
      currentType.value = types[index]
      nextTick(() => {
        // éæ°è®¡ç®é«åº¦
        bscroll && bscroll.refresh()
      })
    }
    // çå¬ä»»ä½ä¸ä¸ªåéæååå°±ä¼è§¦å
    watchEffect(() => {
      nextTick(() => {
        // éæ°è®¡ç®é«åº¦
        bscroll && bscroll.refresh()
      })
    })

    let currentType = ref('sales')
    const showGoods = computed(() => {
      return goods[currentType.value].list
    })
    const bTop = () => {
      bscroll.scrollTo(0, 0, 500)
    }
    return {
      recommends,
      tabClick,
      goods,
      showGoods,
      isTabFixed,
      banref,
      isShowBackTop,
      bTop,
      banners
    }
  }
}
</script>

